import FormFileView from "../../../../optimized/Ui/Input/FormFileView";
import dayjs from "dayjs";
import { localTimeFormat } from "../../../../optimized/Utils/Functions/localTime";
import style from "../../../../optimized/UserStaffMember/FinanceManager/Income/Income.module.css";
import { useTranslation } from "react-i18next";
import { Fragment } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { imageShowUrl } from "../../../../services/BaseUrl";
const StudentMessageActivityDetail = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const onBack = () => {
    navigate(-1);
  };
  return (
    <div className={style.student_message_container}>
      <div
        className={style.student_message_container_inner}
        style={{
          width: "100%",
        }}
      >
        <div className={style.usm_institute_container}>
          <div className={style.usm_institute}>
            <img
              src={
                getQuery.state?.data?.institute?.insProfilePhoto
                  ? `${imageShowUrl}/${getQuery.state?.data?.institute?.insProfilePhoto}`
                  : "/images/ins_default_profile.svg"
              }
              alt="icon"
            />
            <div className={style.student_message_container_inner}>
              <h6>
                {getQuery.state?.data?.institute?.insName ?? "ALD University"}
              </h6>
              <p>
                {dayjs(getQuery.state?.data?.created_at).format("DD MMM, YYYY")}
                {", "}
                {localTimeFormat(getQuery.state?.data?.created_at, "LT")}
              </p>
            </div>
          </div>
          <img src="/images/close-post-icon.svg" onClick={onBack} alt="close" />
        </div>

        <p>{t("title")}</p>
        <h6
          style={{
            whiteSpace: "break-spaces",
          }}
        >
          {getQuery.state?.data?.message_title
            ? getQuery.state?.data?.message_title
                ?.split(" ")
                ?.map((st, index) =>
                  st?.includes("http://") || st?.includes("https://") ? (
                    <a
                      key={index}
                      href={`${st}`}
                      target="_blank"
                      rel="noopener noreferrer"
                      style={{
                        color: "#436dfa",
                        textDecoration: "none",
                      }}
                    >
                      {st}{" "}
                    </a>
                  ) : (
                    <Fragment key={index}>{st} </Fragment>
                  )
                )
            : ""}
        </h6>
        {getQuery.state?.data?.message ? (
          <>
            <p
              style={{
                marginTop: "1rem",
              }}
            >
              {t("message")}
            </p>
            <h6
              style={{
                whiteSpace: "break-spaces",
                fontWeight: "400",
                fontSize: "13px",
              }}
            >
              {getQuery.state?.data?.message
                ? getQuery.state?.data?.message?.split(" ")?.map((st, index) =>
                    st?.includes("http://") || st?.includes("https://") ? (
                      <a
                        key={index}
                        href={`${st}`}
                        target="_blank"
                        rel="noopener noreferrer"
                        style={{
                          color: "#436dfa",
                          textDecoration: "none",
                        }}
                      >
                        {st}{" "}
                      </a>
                    ) : (
                      <Fragment key={index}>{st} </Fragment>
                    )
                  )
                : ""}
            </h6>
          </>
        ) : null}
        {getQuery.state?.data?.message_document ? (
          <FormFileView
            value={
              getQuery.state?.data?.message_document
                ? {
                    key: getQuery.state?.data?.message_document,
                  }
                : ""
            }
            shownLabel="view_attachment"
            customFormInput={{
              fontSize: "13px",
            }}
          />
        ) : null}
        {getQuery.state?.data?.message_mode === "STUDENT_REMINDER" ? (
          <p>
            {t("by")} {" : "}
            {getQuery.state?.data?.message_type === "EMAIL_NOTIFICATION"
              ? "Email"
              : "In App"}
            {", "}({getQuery.state?.data?.from_name ?? ""})
          </p>
        ) : (
          <p>
            {t("by")} {" : "}
            {getQuery.state?.data?.message_type ?? ""}
            {getQuery.state?.data?.from
              ? `(${getQuery.state?.data?.from?.staffFirstName ?? ""} ${
                  getQuery.state?.data?.from?.staffMiddleName ?? ""
                } ${getQuery.state?.data?.from?.staffLastName ?? ""})`
              : ""}
          </p>
        )}
      </div>
    </div>
  );
};

export default StudentMessageActivityDetail;
